import React, { Component } from 'react'
import Barchart from '../barchart'
import JsonData from '../../data/school/学校视角-国际合作-专任教师赴国（境）外指导和开展培训时间（人天）.json'
export default class Sizhengkejiao extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        专任教师赴国（境）外指导和开展培训时间
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData}></Barchart>
                    </div>
                    {/* <div className='item-info'>
                        当前值：5.2/生，比目标值高23%
                                </div> */}
                </div>
            </div>
        )
    }
}
